import React, { useState } from 'react';
import { STRING_TO_URL_CONVERT, HTMLParser, getPlainText, addLineBreaks } from '../../../../../utils';
import HtmlParser from 'react-html-parser';

const PostContent = ({ item }) => {
    const [reactMore, setReactMore] = useState(false);
    //const plainText = getPlainText(item?.postcontent || "");
    //const updateContent = HTMLParser(item?.postcontent);
    const postcontent = item.networkId == 20 ? item?.postcontent : STRING_TO_URL_CONVERT(item?.postcontent);
    const content = Array.isArray(postcontent) ? postcontent.length > 0 ? postcontent[0] : "" : postcontent;
    const notMoreText = content.length > 80 ? true : false;
    const newPostContent = item.networkId == 20 ? window.slackdown.parse(content, item.slackMember) : content;

    return (
        <>{notMoreText ?
            <><span className={`mb-1 gridpostContent ${reactMore ? 'trimPostContent2 ' : 'trimPostContent '}`} id={`gridpostContent${item.id}`}>{HtmlParser(addLineBreaks(String(newPostContent) || ""))}</span>
                <span className="readMore_btn position-relative text-muted fs-9 fw-semibold cursor-pointer" onClick={(e) => { e.stopPropagation(); setReactMore(!reactMore) }}> {reactMore ? 'Read Less' : '... Read More'}</span></>
            : <p id={`gridpostContent${item.id}`} className="gridpostContent fs-8"> {HtmlParser(addLineBreaks(String(newPostContent) || ""))}</p>}
        </>

    )
}

export default PostContent;