import { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import getImageSize from 'react-image-size';
import { FACEBOOK_CLIENT_ID } from '../../../../../actions/constants';
import { MEDIA_UPDATE_REQ } from '../../../../../actions/moderationActions';

const VideoType = ({ item, mediaClass, size, height, width, url, poster, autoPlay, muted, isCover, controls, loop, isupdate }) => {

  const [videoLoaded, setVideoLoaded] = useState(false)
  const [padding, setPadding] = useState(size ? size : 100)

  const onMediaLoad = async () => {
    if (size) {
      setPadding(size)
    } else {
      if (height && height != "" && width && width != "") {
        setPadding((height * 100) / width)
      } else {
        try {
          const { width, height } = await getImageSize(poster);
          setPadding((height * 100) / width)
        } catch (error) {
          setPadding(100)
        }
      }
    }
  }

  const videoLoadedFun = () => {
    setVideoLoaded(true)
  }

  useEffect(() => {
    onMediaLoad()
  }, [size])

  return <div className={`${mediaClass} overflow-hidden position-relative`} style={{ paddingBottom: `${padding}%` }}>
    <div className="position-absolute object-center-cover h-100 w-100">
    {/* <div className="position-absolute h-100" style={{ left: '0', right: '0', width: 'auto !important' }}> */}
      <ReactPlayer
        poster={poster}
        className={''}
        url={url}
        config={{
          facebook: { appId: FACEBOOK_CLIENT_ID },
          attributes: { 'data-item-id': item?.id, 'poster': poster, 'data-network': item?.networkId, 'data-stories': item?.stories, "data-load": '0', "data-filter": item?.feedFilter, "data-wall-id": item?.wallId, 'data-type': 'video' }
        }}
        autoPlay={autoPlay}
        muted={true}
        volume={autoPlay ? 1 : 0}
        playsinline={true}
        onReady={videoLoadedFun}
        playing={autoPlay}
        height={'100%'}
        width={'100%'}
        // width={'auto'}
        loop={loop}
        controls={controls}
        onError={isupdate ? MEDIA_UPDATE_REQ : null}
        style={{ backgroundImage: (!videoLoaded) ? `url(${poster})` : '', backgroundSize: isCover ? 'cover' : 'contain', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', textAlign: 'center' }}
      />
    </div>
  </div>
}
export default VideoType