import React, { Component } from 'react'
import { connect } from "react-redux";
import Header from './Header'
import { Container } from 'react-bootstrap';
import { getModeration, updateCheckAll, selectPostUpdate, activeTabActionFilter, dateRangeFilter, clickToPostStatusALL, clickToPostDeleteALL, searchTagsFilter, limitUpdate, onNextNavigateClick, onPrevNavigateClick, onUpdatePage, clickToRefresh, removeAllFilterBody, searchPostFilter, removeCheckAll, updateCheckStatusAll, CLEAR_UGC_REQUEST, CLEAR_UGC_DATA, ugcModal, postCarosal } from '../../../../actions/moderationActions'
import { fadeOutEffect, shopifyApp, SWAL_FIRE } from '../../../../utils'
import { FILTER_LOADER_UPDATE, TOGGLE_ADD_GALLERY } from '../../../../reducer/keys';
import { getUserInfo_afterChanges } from '../../../../actions/loginActions';
import withRouter from '../../../routes/WithRouter';
import './moderation.scss';
import FilterBody from './Filters';
import FilterTab from './Filters/filterTab';
import AddUgcRights from './UgcRights';
import UgcStatus from './UgcRights/Status';
import Skeleton from './Elements/Skeleton'
import MasonryView from './MasonryView'
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon';
import ErrorFound from '../../../ErrorFound';
import { checkPermission } from '../../../../actions/collaboratorActions';
import { GALLERY_WALL_UPDATE } from '../../../../actions/sceneActions';
import SubHeaderCrumbs from '../CommonComponents/SubHeader';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import SideMenu from './SideMenu';

class Moderation extends Component {
  allImageQueue = [];
  state = { total: 0, isCheckAll: false, isCheckedPostCount: 0, perPageCount: 10, activePage: 1, filters: [], allFirstImage: false, statusPopup: 0, showUgcModal: false }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.moderation.changeModeration_wall !== this.props.moderation.changeModeration_wall && this.props.moderation.changeModeration_wall) this.setState({ activeTab: this.props.moderation?.filter?.status }, this.clickToUncheckAll)
    if (this.props?.allWalls?.length !== prevProps?.allWalls?.length) this.props.getModeration();
  }

  componentWillMount() {
    const { params } = this.props;
    const currentUrl = window.location.href;
    const newUrl = currentUrl.replace(params?.id, localStorage.getItem('wallId'));
    window.history.pushState(null, '', newUrl);
    window.feedId = params.feedId;
  }

  componentDidMount() {
    if (window.location.href.includes("ob_success")) this.onBoardingSuccess()
    document.cookie = "callBackUrl=React";
    this.props.postLoader()
    if(this.props?.allWalls?.length) this.props.getModeration();
  }

  componentWillUnmount() {
    this.allImageQueue = [];
    this.props.removeAllFilterBody(true)
    this.props.removeCheckAll(false)
    this.props.limitUpdate(50, true)
  }

  onBoardingSuccess = () => {
    setTimeout(() => {
      SWAL_FIRE({
        icon: 'success',
        title: `<strong>You are almost there!</strong>`,
        html: `Tag products to curate a Shoppable Gallery & publish it on your pages.`,
        showCloseButton: true,
        showCancelButton: false,
        focusConfirm: true,
        confirmButtonText: `Got it!`,
        confirmButtonColor: `var(--bs-success)`,
        confirmButtonAriaLabel: `Got it!`,
        cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
        cancelButtonAriaLabel: 'Thumbs down'
      }).then(result => {
        if (result.isConfirmed) { this.props.getUserInfo_afterChanges(); this.props.navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`) }
      })
    }, 1000);
  }

  clickToCheckAll = event => {
    const { isCheckAll } = this.state;
    this.setState({ isCheckAll: !isCheckAll }, () => {
      this.onActionUpdateOpcity()
      this.props.updateCheckAll(this.state.isCheckAll)
    })
  }

  clickToCheckStatusAll = (status) => event => {
    this.onActionUpdateOpcity()
    this.props.updateCheckStatusAll(status)
  }

  clickToUncheckAll = event => {
    this.setState({ isCheckAll: false }, () => {
      this.onActionUpdateOpcity()
      this.props.removeCheckAll(false)
    })
  }

  clickPostChecked = (index) => event => this.props.selectPostUpdate(index, this.onActionUpdateOpcity())

  onActionUpdateOpcity = () => {
    const { isCheckAll, isCheckedPostCount } = this.state;
    if (isCheckAll || (isCheckedPostCount > 0)) {
      if (document.getElementsByClassName("modSelectNo") && document.getElementsByClassName("modSelectNo")[0])
        fadeOutEffect(document.getElementsByClassName("modSelectNo")[0])
      if (document.getElementsByClassName("filter-bar") && document.getElementsByClassName("filter-bar")[0]) fadeOutEffect(document.getElementsByClassName("filter-bar")[0])
    }
  }

  clickToTab = (activeTab) => event => this.props.activeTabActionFilter(activeTab)

  onDateRangeFilter = (startDate, endDate) => this.props.dateRangeFilter(startDate, endDate)

  onClickToFilterUpdateStatus = (status) => event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToPostStatusALL(status) }
  onClickToFilterDeleteAll = event => { if (checkPermission('contentGallery', 'editGallery', true)) this.props.clickToPostDeleteALL(this.props.navigate) }
  onClickNextPage = event => this.props.onNextNavigateClick()
  onClickPrevPage = event => this.props.onPrevNavigateClick()
  onChangePage = (page) => event => { this.props.onUpdatePage(page) }
  openUgcModal = (status) => this.setState({ showUgcModal: status })

  onClickReset = (item) => {
    let tempFilter = this.state.filters.filter(i => i.key === item.key)
    tempFilter = tempFilter.filter((it) => {
      if (["postType", "socialFeed", "other"].includes(it.key))
        return it.value.filter((j) => !item.value.includes(j));
    });
    this.setState({ filters: tempFilter }, () => this.props.clickToRefresh())
  }

  onClickAllReset = event => {
    this.setState({ filters: [] }, () => this.props.getModeration())
  }

  updatepostCarosal = (status) => {
    const { allWalls, galleryUpdate, postCarosal } = this.props
    postCarosal(status)
    const getWallData = allWalls?.length && allWalls.find((e) => e.id == localStorage.getItem('wallId'))
    if (getWallData) galleryUpdate({ name: getWallData.name, status: getWallData?.status, carouselPost: status || 0, autoUpdate: getWallData.autoUpdate || 0, deleted: 0, id: getWallData.id, action: 2 })
  }

  openugcModel = () => this.props.ugcModal(true)
  onShow = (id) => this.setState({ statusPopup: id })

  render() {
    const { isCheckAll, perPageCount, activePage, filters, showUgcModal } = this.state;
    const { moderation, searchTagsFilter, limitUpdate, loader, clickToRefresh, clearUgcData, removeAllFilterBody, searchPostFilter, allWalls, wallsLoader, feeds, ugcData, CLEAR_UGC_REQUEST_DATA, navigate,isShoppable } = this.props;
    const appContent = window?.APP_CONTENT?.moderation ?? {};
    const getWallData = allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};
    const isShopify = shopifyApp();
    const breadCrumbs = window?.APP_CONTENT?.['sideBar'];
    
    return (
      <>

        {allWalls?.length ? <SubHeaderCrumbs key={getWallData?.id} crumbs={[{ name: getWallData?.name ?? "", icon: "image", imgUrl: getWallData?.thumbnail ?? "" }, { name: breadCrumbs?.home, link: "/home" }, { name:breadCrumbs?.contentGallery, link: "/content" }]} showGallerySwitcher={true} /> : null}
        <div className={`custom-side-menu d-flex`}>
          <SideMenu {...{ getWallData }} />

          <div className="custom-main-content" >
            {!wallsLoader && !(allWalls && allWalls.length > 0) ?
              <div className="f-center" style={{ minHeight: 'calc(100vh - 182px)' }}>
                <EmptyWithIcon title={feeds && feeds.length ? appContent?.noPosts : appContent?.createGallery} desc={appContent?.deletedGallery} icon={'media'} button={{ action: () => checkPermission('contentGallery', 'createGallery', true) ? this.props.TOGGLE_ADD_GALLERY(true) : {}, text: appContent?.btn_create_gallery }} isFullScreen={false} />
              </div>
              :
              <>
                {(moderation?.total) || loader.filterLoader ? <Header postCarosal={this.updatepostCarosal} clickToRefresh={clickToRefresh} isShoppable={isShoppable} allWalls={allWalls} clickToCheckAll={this.clickToCheckStatusAll} clickToUncheckAll={this.clickToUncheckAll} isCheckAll={isCheckAll} isCheckedPostCount={moderation?.isCheckedPostCount} searchPostFilter={searchPostFilter} onClickToViewFilter={this.onClickToViewFilter} total={moderation?.total} perPageCount={perPageCount} activePage={activePage} moderation={moderation} clickToTab={this.clickToTab} onDateRangeFilter={this.onDateRangeFilter} onClickToFilterUpdateStatus={this.onClickToFilterUpdateStatus} onClickToFilterDeleteAll={this.onClickToFilterDeleteAll} searchTagsFilter={searchTagsFilter} limitUpdate={limitUpdate} onNextNavigateClick={this.onClickNextPage} onPrevNavigateClick={this.onClickPrevPage} onChangePage={this.onChangePage} onAppliedFilter={this.onAppliedFilter} openugcModel={this.openUgcModal} activeTab={moderation?.filter?.status} key={moderation?.filter?.status} /> : null}
                <div className="d-flex flex-column flex-column-fluid full-content moderation__">
                  <Container fluid className='position-relative'>
                    <FilterBody {...this.props} filterBody={moderation.filterBody} />
                    <FilterTab  {...this.props} filterBody={moderation.filterBody} />
                    <UgcStatus {...this.props} statusPopup={this.state.statusPopup} onHide={() => this.setState({ statusPopup: 0}, CLEAR_UGC_REQUEST_DATA)} openugcModel={this.openUgcModal} />

                    {moderation?.total == 0 && !moderation?.loding && !loader.filterLoader && feeds?.length == 0 ? <div className="f-center" style={{ minHeight: 'calc(82vh - 241px)' }}>
                        <EmptyWithIcon title={appContent?.no_feed} desc={appContent?.no_feed_desc} icon={null} button={{ action: () => checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}, text: appContent?.btn_add_feed }} isHelp={true} isFullScreen={false} />
                      </div> :
                    
                    moderation?.total == 0 && !moderation?.loding && !loader.filterLoader && ((feeds?.length == 1 && feeds?.[0]?.networkId == 18 && feeds?.[0]?.filterId == 24) || (moderation?.filterBody?.socialFeed?.[0]?.key?.includes('- Mention') && feeds?.find(item => item.id == moderation?.filterBody?.socialFeed?.[0]?.value)?.networkId == 18)) ? <div className="f-center" style={{ minHeight: 'calc(100vh - 241px)' }}>
                        <EmptyWithIcon title={appContent?.meta_limit} desc={appContent?.meta_limit_desc} icon={null} button={{ action: () => checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}, text: appContent?.btn_add_feed2 }} isHelp={true} isFullScreen={false} />
                      </div> :
                      moderation?.total == 0 && !moderation?.loding && !loader.filterLoader ? <div className="f-center" style={{ minHeight: `calc(${getWallData?.totalFeed ? '100' : '82'}vh - 241px)` }}>
                        <EmptyWithIcon title={appContent?.no_posts_avail} desc={appContent?.no_posts_avail_desc} icon={null} button={{ action: () => checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}, text: appContent?.btn_add_feed3 }} isHelp={true} isFullScreen={false} />
                      </div>
                      : (moderation?.listData?.length == 0 && moderation?.filter?.status == 1 && !loader.filterLoader) ? <div className="f-center" style={{ minHeight: 'calc(100vh - 182px)' }}>
                        <EmptyWithIcon title={appContent?.private_posts} desc={appContent?.private_posts_desc} icon={null} isHelp={true} button={{ action: () => checkPermission('networkManager', 'manageFeed', true) ? navigate(`/content/addfeed/${localStorage.getItem('wallId')}`) : {}, text: appContent?.btn_add_feed4 }} isFullScreen={false} />
                      </div> : (moderation?.listData?.length == 0 && moderation?.filter?.status == 2 && !loader.filterLoader) ? <div className="f-center" style={{ minHeight: 'calc(100vh - 182px)' }}>
                        <EmptyWithIcon title={appContent?.no_posts_yet} desc={appContent?.no_posts_yet_desc} icon={'media'} button={null} isFullScreen={false} />
                      </div> :
                        <div className="moderation_card tab-content pb-10 pb-lg-0 h-100">
                          {loader.filterLoader ? <Skeleton {...this.props} /> : <ErrorFound><MasonryView clickPostChecked={this.clickPostChecked}  {...moderation} removeAllFilterBody={removeAllFilterBody} activeTab={moderation?.filter?.status} searchTagsFilter={searchTagsFilter} onAppliedFilter={this.onAppliedFilter} filters={filters} onClickAllReset={this.onClickAllReset} onClickReset={this.onClickReset} onShow={this.onShow} navigate={navigate} openUgcModal={this.openUgcModal} /></ErrorFound>}
                        </div>}

                    {(showUgcModal && ugcData?.template?.length) ? <ErrorFound><AddUgcRights ugcData={ugcData} hideUgc={clearUgcData} /></ErrorFound> : null}
                  </Container>
                </div>
              </>}
          </div>
        </div>
      </>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    moderation: state.moderation,
    isShoppable: state.authorized?.userData?.user_data?.UserRule?.shoppableStatus,
    loader: state.loader,
    allFont: state.allFont,
    showAddFeed: state.modalReducer?.showAddFeed,
    showManageFeed: state.modalReducer?.showManageFeed,
    showCta: state.modalReducer?.showCta,
    wallsLoader: state.dashboardData?.wallsLoader,
    addGalleryNow: state.dashboardData?.addGalleryNow,
    ctaData: state.ctaData?.data,
    ugcData: state.moderation?.ugcData,
    ugcRequestData: state.moderation?.ugcRequestData,
    feeds: state.manageFeeds?.feeds,
    allWalls: state.dashboardData?.dashboarddata?.allWalls,

  }
}
const mapDispatchToProps = dispatch => {
  return {
    getModeration: (callback) => dispatch(getModeration(callback)),
    postLoader: () => dispatch({ type: FILTER_LOADER_UPDATE, payload: true }),
    activeTabActionFilter: (status) => dispatch(activeTabActionFilter(status)),
    updateCheckAll: (status) => dispatch(updateCheckAll(status)),
    selectPostUpdate: (index, callBack) => dispatch(selectPostUpdate(index, callBack)),
    dateRangeFilter: (startDate, endDate) => dispatch(dateRangeFilter(startDate, endDate)),
    clickToPostStatusALL: (status) => dispatch(clickToPostStatusALL(status)),
    clickToPostDeleteALL: (navigate) => dispatch(clickToPostDeleteALL(navigate)),
    searchTagsFilter: (searchTags) => dispatch(searchTagsFilter(searchTags)),
    searchPostFilter: (searchPost) => dispatch(searchPostFilter(searchPost)),
    limitUpdate: (limit, stopApiCall) => dispatch(limitUpdate(limit, stopApiCall)),
    onNextNavigateClick: () => dispatch(onNextNavigateClick()),
    onPrevNavigateClick: () => dispatch(onPrevNavigateClick()),
    onUpdatePage: (page) => dispatch(onUpdatePage(page)),
    clickToRefresh: () => dispatch(clickToRefresh()),
    postCarosal: (status) => dispatch(postCarosal(status)),
    removeAllFilterBody: (status) => dispatch(removeAllFilterBody(status)),
    removeCheckAll: (data) => dispatch(removeCheckAll(data)),
    updateCheckStatusAll: (data) => dispatch(updateCheckStatusAll(data)),
    TOGGLE_ADD_GALLERY: (status) => dispatch({ type: TOGGLE_ADD_GALLERY, payload: status }),
    getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
    clearUgcData: () => dispatch(CLEAR_UGC_DATA()),
    galleryUpdate: (action) => dispatch(GALLERY_WALL_UPDATE(action)),
    CLEAR_UGC_REQUEST_DATA: () => dispatch(CLEAR_UGC_REQUEST()),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(React.memo(withRouter(Moderation)));
