import React, { Component } from 'react'
import { connect } from "react-redux";
import parse from 'react-html-parser';
// import { generateSocialName } from '../../Feeds/utils'
import { generateRandomArray, GET_NETWORK_ICON, TOAST_ERROR } from '../../../../../utils';
import PostTagSearch from './postTagSearch'
import { updateFilterPostTags, updateFilterSocialFeeds, updateFilterPostType, updateFilterOthers, removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeAllFilterBody, aiFilter, removeAiFilter, sentimentFilter, removeSentimentFilter, ratingFilter, removeRatingFilter, postLangFilter, removePostLangFilter } from '../../../../../actions/moderationActions'
import { Accordion, Offcanvas, Button, Image, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import Rating from '../../CommonComponents/Rating';
import { LANGUAGE_SUPPORTS } from '../../../../../actions/constants';
import { MediaBaseUrl } from '../../../../../actions/api';


const PostTypeCheck = ({ value, onClickPostType, moderation, label, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.postType.length > 0 ? moderation.filterBody.postType.filter((fin) => fin == value) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <Form.Check key={id} type="checkbox" className='mb-4' value="1" label={label} id={id} onChange={onClickPostType(value, isChecked)} checked={isChecked} />
}

const OtherInput = ({ value, onClickOther, moderation, label, id }) => {
    const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.other.length > 0 ? moderation.filterBody.other.filter((fin) => fin.key == id) : []
    const isChecked = selecteds && selecteds.length > 0 ? true : false
    return <Form.Check key={id} type="checkbox" className='mb-4' value="1" label={label} id={id} onChange={onClickOther(id, value, isChecked)} checked={isChecked} />
}

const AiCheck = ({ value, onClickAi, moderation, label, id }) => {
    const isChecked = moderation.filterBody?.recommendation.includes(value)
    const checklabel = <><span className={`d-inline-block h-10px w-10px rounded-circle me-2 bg-${value == '1' ? 'success' : value == '2' ? 'warning' : 'danger'}`}></span>{label}</>
    return <div className='f-center justify-content-start' key={id}>
        <Form.Check type="checkbox" className='mb-4' value="1" label={checklabel} id={id} onChange={onClickAi(value, isChecked)} checked={isChecked} />
    </div>
}

const SentimentCheck = ({ value, onClickSentiment, moderation, label, id }) => {
    const isChecked = moderation.filterBody?.sentiment.includes(value);
    const checklabel = <React.Fragment key={`fragment-${id}`}><FontAwesomeIcon icon={`face-${value == '1' ? 'smile' : value == '2' ? 'meh' : 'frown'}`} iconClass="text-muted fs-8 d-inline-block me-1" />{label}</React.Fragment>
    return <div className='f-center justify-content-start' key={id}>
        <Form.Check type="checkbox" className='mb-4' value="1" label={checklabel} id={id} onChange={onClickSentiment(value, isChecked)} checked={isChecked} />
    </div>
}

const RatingCheck = ({ value, onClickRating, moderation, label, id }) => {
    const isChecked = moderation.filterBody?.rating?.includes(value);
    const checklabel = <div key={`fragment-${id}`} className="d-flex "><Rating rating={value} /> <span className="text-muted fs-8 d-inline-block me-1 ms-2">{`${value} Star`}</span></div>
    return <div className='f-center justify-content-start' key={id}>
        <Form.Check type="checkbox" className='mb-4' value="1" label={checklabel} id={id} onChange={onClickRating(value, isChecked)} checked={isChecked} />
    </div>
}

class FilterBody extends Component {
    constructor(props) {
        super(props);
        this.state = { isActiveAside: false }
        this.toogleFilter = this.toogleFilter.bind(this);
    }


    toogleFilter = () => {
        this.setState({ isActiveAside: !this.state.isActiveAside, })
        setTimeout(() => {
            var offcanvas = document.querySelector(".offcanvas-backdrop");
            if (offcanvas) {
                offcanvas.classList.add('filter-backdrop');
            }
        }, 100);
    };

    onSelectFeeds = (key, value, status) => event => {
        if (!status) this.props.updateFilterSocialFeeds({ key, value })
        else this.props.removeFilterSocialFeeds({ key, value })
    }
    onClickPostType = (value, status) => event => {
        if (!status) this.props.updateFilterPostType(value)
        else this.props.removeFilterPostType(value)
    }
    onClickOther = (key, value, status) => event => {
        if (key == 'tagProduct') {
            const foundItem = this.props.allWalls?.length && this.props.allWalls.find(item => String(item.id) == String(localStorage.getItem('wallId')));
            if (foundItem?.carouselPost) return TOAST_ERROR('To apply this filter, please disable the Carousel feature first.')
        }
        if (!status) this.props.updateFilterOthers({ key, value })
        else this.props.removeFilterOthers({ key, value })
    }

    onClickAi = (value, status) => event => {
        if (!status) this.props.aiFilter(value)
        else this.props.removeAiFilter(value)
    }

    onClickSentiment = (value, status) => event => {
        if (!status) this.props.sentimentFilter(value)
        else this.props.removeSentimentFilter(value)
    }

    onClickRating = (value, status) => event => {
        if (!status) this.props.ratingFilter(value)
        else this.props.removeRatingFilter(value)
    }

    onClickLanguage = (value, status) => event => {
        if (!status) this.props.postLangFilter(value)
        else this.props.removePostLangFilter()
    }

    componentDidMount() {
        this.addEventListeners();

        if (window.feedId && window.feedId != "") {
            const { manageFeeds } = this.props
            const feeds = manageFeeds?.feeds || [];
            if(feeds.length > 0){
                feeds.forEach(item => { if (window.feedId == item.id) this.props.updateFilterSocialFeeds({ key: item.name, value: window.id }) })
            }
        }
    }

    componentDidUpdate() { this.addEventListeners(); }

    addEventListeners() {
        const filterAside = document.getElementById("filter_aside");
        if (filterAside) {
            filterAside.addEventListener("click", this.toogleFilter);
        }
    }

    render() {
        const { moderation, manageFeeds } = this.props
        const { isActiveAside } = this.state
        const UserRuleEnableAI = false;
        const UserRuleManualTag = true;
        const postTags = moderation?.filterBody?.postTags;
        const { userDetail } = this.props
        const truSense = userDetail?.truSense
        const { feeds, postLanguageData } = manageFeeds || {};
        const reviewFeed = feeds ? feeds?.some(item => [4, 38, 19, 23, 35, 34, 37, 29].includes(item.networkId)) ?? false : false;
        const appContent = window?.APP_CONTENT?.moderation ?? {};
        
        return <Offcanvas show={isActiveAside} placement={'end'} onHide={this.toogleFilter} className={`filter_aside ${(userDetail?.user?.isPaymentFailed && userDetail?.user?.isPaymentFailedUrl && userDetail?.user?.isPaymentTypeUser) ? 'appeared-review-offcanvas' : 'dis-appeared-review-offcanvas'}`}>
            <Offcanvas.Body>
                <div className="f-between px-4 py-3 border-bottom">
                    <span className="fs-6 fw-medium">{appContent?.filter_add || ''}</span>
                    <Button variant='icon' size='sm' className='btn-active-primary' onClick={this.toogleFilter}>
                        <FontAwesomeIcon icon={'xmark'} />
                    </Button>
                </div>
                {(UserRuleEnableAI || UserRuleManualTag) ?
                    <Accordion className="border-bottom" flush>
                        <Accordion.Item defaultactivekey="0">
                            <Accordion.Header>
                                <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_tags || ''}</span>
                            </Accordion.Header>
                            <Accordion.Body className="px-5 py-1">
                                {UserRuleEnableAI &&
                                    <Image className='mb-3' src={baseUrl('media/svg/trusense.svg')} height={23} width={120} fluid />
                                }
                                <div id="tag_post_btn" className="collapse show" data-parent="#tagspost">
                                    <PostTagSearch content={appContent} postTags={postTags} {...this.props} />
                                </div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                    : null}
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_feeds || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <div id="social_feed_btn" className="collapse show" data-parent="#post_type_drop">
                                <div className="checkbox-list">
                                    {
                                        feeds?.map((item, index) => {
                                            const selecteds = Object.keys(moderation.filterBody).length > 0 && moderation.filterBody.socialFeed.length > 0 ? moderation.filterBody.socialFeed.filter((fin) => fin.value == item.id) : []
                                            const isChecked = selecteds && selecteds.length > 0 ? true : false
                                            return item.status ? <Form.Label key={index} className='d-flex align-items-start mb-4' htmlFor={`moderationfeed${item.id}`}>
                                                <Form.Check type="checkbox" id={`moderationfeed${item.id}`} value={item.id} data-filter={item.filterId} onChange={this.onSelectFeeds(item.name, item.id, isChecked)} checked={isChecked} style={{ marginTop: 2 }} />
                                                <Image className='mx-2' src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} fluid />
                                                <span className='fil_network'>{parse(item.name)}</span>
                                            </Form.Label> : ''
                                        })
                                    }

                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                {postLanguageData?.length > 0 ? <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_language || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <div id="lang_post_btn" className="collapse show" data-parent="#post_type_drop">
                                <div className="checkbox-list">
                                    {
                                        postLanguageData?.map((item, index) => {
                                            const currentItem = LANGUAGE_SUPPORTS.find((fin) => fin.value == item);
                                            const isChecked = moderation?.filterBody?.languageCodes.value == item;
                                            return currentItem ? <Form.Label key={index} className='d-flex align-items-start mb-4' htmlFor={`moderationLang${index}`}>
                                                <Form.Check type="checkbox" id={`moderationLang${index}`} value={item} onChange={this.onClickLanguage(currentItem, isChecked)} checked={isChecked} style={{ marginTop: 2 }} />
                                                <Image className='mx-2 border' src={`${MediaBaseUrl}media/images/flags/${currentItem?.value}.svg`} height={20} width={20} fluid />
                                                <span className='fil_network'>{currentItem?.label ?? ""}</span>
                                            </Form.Label> : null
                                        })
                                    }
                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion> : null}

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_type || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <PostTypeCheck key={`00`} value="1" onClickPostType={this.onClickPostType} {...this.props} label={`${appContent?.type_text || ''}`} id={`text_only`} />
                            <PostTypeCheck key={`01`} value="2,4" onClickPostType={this.onClickPostType} moderation={moderation} label={`${appContent?.type_image || ''}`} id={`image_only`} />
                            <PostTypeCheck key={`02`} value="3,5" onClickPostType={this.onClickPostType} moderation={moderation} label={`${appContent?.type_video || ''}`} id={`video_only`} />
                            <OtherInput key={`03`} value="1" onClickOther={this.onClickOther} moderation={moderation} label={`${appContent?.type_ugc || ''}`} id={`tagProduct`} />
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                
                    <Accordion className="border-bottom" flush>
                        <Accordion.Item defaultactivekey="0">
                            <Accordion.Header>
                                <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_ai || ''}</span>
                            </Accordion.Header>
                            <div style={{ position: 'relative' }}>
                                {!truSense && (
                                    <div style={{position: 'absolute',top: '45%',left: '50%',transform: 'translate(-50%, -50%)',zIndex: 1,textAlign: 'center'}}>
                                        <Link to="/price" className="btn btn-sm btn-primary">{appContent?.btn_upgrade || ''}</Link>
                                    </div>
                                )}
                                <div style={{filter: !truSense ? 'blur(4px)' : 'none',pointerEvents: !truSense ? 'none' : 'auto', opacity: !truSense ? 0.7 : 1}}>
                                    <Accordion.Body className="px-5 py-1">
                                        {generateRandomArray(3).map((_, index) => <AiCheck key={index} value={String(index + 1)} onClickAi={this.onClickAi} {...this.props} label={index == 0 ? `${appContent?.ai_recom || ''}` : index == 1 ? `${appContent?.ai_consider || ''}` : `${appContent?.ai_not_recom || ''}`} id={index == 0 ? `recom` : index == 1 ? `consider` : 'not_recom'} />)}
                                    </Accordion.Body>
                                </div>
                            </div>
                        </Accordion.Item>
                    </Accordion>

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_sentiment || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {generateRandomArray(3).map((_, index) => <SentimentCheck key={index} value={String(index + 1)} onClickSentiment={this.onClickSentiment} {...this.props} label={index == 0 ? `${appContent?.sent_pos || ''}` : index == 1 ? `${appContent?.sent_neut || ''}` : `${appContent?.sent_neg || ''}`} id={index == 0 ? `pos` : index == 1 ? `neut` : 'neg'} />)}
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                {reviewFeed ? <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_rating || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {generateRandomArray(5).map((_, index) => <RatingCheck key={index} value={5 - index} onClickRating={this.onClickRating} {...this.props} id={5 - index} />)}
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion> : null}

                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header>
                            <span className="text-uppercase fs-8 fw-medium">{appContent?.filter_other || ''}</span>
                        </Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <OtherInput key={0} value="1" onClickOther={this.onClickOther} moderation={moderation} label={`${appContent?.other_pin || ''}`} id={`pinFilter`} />
                            <OtherInput key={1} value="1" onClickOther={this.onClickOther} moderation={moderation} label={`${appContent?.other_recent || ''}`} id={`recentFilter`} />
                            {/* <OtherInput key={2} value="1" onClickOther={this.onClickOther} moderation={moderation} label={`${appContent?.other_retweets || ''}`} id={`retweetsFilter`} /> */}
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <div className="d-flex flex-column mx-5 py-4">
                    <Button variant='link' onClick={this.props.removeAllFilterBody} className="">{appContent?.btn_reset || ''}</Button>
                </div>
            </Offcanvas.Body>
        </Offcanvas>

    }
}
const mapStateToProps = (state) => {
    return {
        moderation: state.moderation,
        manageFeeds: state.manageFeeds,
        allWalls: state.dashboardData?.dashboarddata?.allWalls
    }
}
const mapDispatchToProps = dispatch => {
    return {
        updateFilterPostTags: (data) => dispatch(updateFilterPostTags(data)),
        updateFilterSocialFeeds: (data) => dispatch(updateFilterSocialFeeds(data)),
        updateFilterPostType: (data) => dispatch(updateFilterPostType(data)),
        updateFilterOthers: (data) => dispatch(updateFilterOthers(data)),
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
        aiFilter: (data) => dispatch(aiFilter(data)),
        removeAiFilter: (data) => dispatch(removeAiFilter(data)),
        sentimentFilter: (data) => dispatch(sentimentFilter(data)),
        removeSentimentFilter: (data) => dispatch(removeSentimentFilter(data)),
        ratingFilter: (data) => dispatch(ratingFilter(data)),
        removeRatingFilter: (data) => dispatch(removeRatingFilter(data)),
        postLangFilter: (data) => dispatch(postLangFilter(data)),
        removePostLangFilter: () => dispatch(removePostLangFilter()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterBody);