import React, { Component } from 'react';
import { Form, OverlayTrigger, Tooltip, Button, Image, Badge } from 'react-bootstrap';
import { formatTemplateText, GET_FIRST_LETTER, HTMLParser, TOAST_ERROR } from '../../../../../../utils';
import CreatableSelect from 'react-select/creatable';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { autoCompleteSearch } from '../../../../../../actions/moderationActions';
import { DashboardModal } from '@uppy/react';
import UppyUpload from '../../../../../../utils/uppyUpload';
import $ from 'jquery'
import { WASABI_PATH } from '../../../../../../actions/api';
import { TEMP_FOLDER } from '../../../../../../actions/constants';
import AttachImage from '../../../CommonComponents/Uploads/AttachImage';
import './editElement.scss'
import Uploader from '../../../CommonComponents/Uploader';

const uppyUploader = new UppyUpload()
class EditAside extends Component {
       constructor(props) {
              super(props);
              this.state = { tagString: [], searchPost: '', selectedOption: [], isLoading: false, uppyProfileOpen: false, profile: '' };
       }

       componentDidMount() {
              if (uppyUploader) {
                     uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
                            this.props.onChangeAuthorImg(`${WASABI_PATH}${TEMP_FOLDER}${result.id}.${result.ext}`)
                            setTimeout(() => {
                                   uppyUploader.uppy.cancelAll()
                                   this.setState({ uppyProfileOpen: false })
                            }, 300)
                     })
              }
              this.getTagsArray()
       }

       getTagsArray = () => {
              let submitTags = (this.props?.tags || "").split(",").filter(tag => tag.trim().length > 0);
              this.setState({ tagString: submitTags });

       }
       componentDidUpdate(prevProps) {
              if (prevProps.tags !== this.props.tags) {
                     this.getTagsArray();
              }
       }

       onLoad = () => {
              const { searchPost } = this.state;
              if (searchPost?.length > 0) {
                     this.setState({ isLoading: true });

                     autoCompleteSearch(searchPost).then((response) => {
                            const { responseData, responseCode } = response.data;
                            if (responseCode === 200) {
                                   const formattedTags = responseData?.totalTag.map(tag => ({ value: tag, label: tag.charAt(0).toUpperCase() + tag.slice(1) }));
                                   this.setState({ selectedOption: formattedTags, isLoading: false });
                            } else {
                                   this.setState({ selectedOption: [], isLoading: false });
                            }

                     }).catch(() => {
                            this.setState({ isLoading: false });
                     });
              } else {
                     this.setState({ selectedOption: [], isLoading: false });
              }
       }


       onSearchText = (textInput) => { this.setState({ searchPost: textInput }, () => this.onLoad()); }

       handleSelectChange = (e) => {
              const { tagString } = this.state;
              if (e?.value?.length) {
                     if (tagString.includes(e.value)) {
                            TOAST_ERROR('Tag already exists');
                     } else {
                            this.setState({ tagString: [...tagString, e.value] }, () => this.props.onChangeTag(this.state.tagString.join(',')));
                     }
              }
       };



       onClickToRemoveTag = (index, tag) => event => {
              let { tagString } = this.state;
              tagString.splice(index, 1)
              this.props.onRemoveTag(tag);
              this.setState({ tagString }, () => this.props.onChangeTag(tagString.join(',')))
       }

       handleOpen = event => {
              this.setState({ uppyProfileOpen: true, })
              $(".uppy-Dashboard-close").click(() => {
                     uppyUploader.uppy.cancelAll()
                     this.setState({ uppyProfileOpen: false })
              })
       }

       handleSave = () => {
              const { author, handle, onSavePost } = this.props;
              if (author && handle) onSavePost();
       };


       isValidNewOption = (inputValue) => {
              return inputValue.length >= 3;
       };

       render() {
              const { onChangeAuthor, onChangeHandle, onChangeContent, onClickRemoveAuthorImg, author, handle, authorImage, onSavePost, onSaveTag, content, handleClose, loading, isTaggModal, updatePost, modalData, onChangePost } = this.props
              const { tagString, isLoading, selectedOption } = this.state;
              const appContent = window?.APP_CONTENT ?? {};

              const tagsArrayFun = () => {
                     const tagItem = tagString.map((tag, removeIndex) =>
                            <Badge key={tag.toString()} className="me-1 mb-1 px-3 py-2 f-center bg-secondary">
                                   <span className=' text-ellipsis d-block'>{tag}</span>
                                   <Button variant='link' size='sm' className="p-0 ms-1 " onClick={this.onClickToRemoveTag(removeIndex, tag)}>
                                          <FontAwesomeIcon icon={'xmark'} />
                                   </Button>
                            </Badge>
                     );
                     return tagItem;
              }
              return (
                     <div>
                            <div className='p-4 m_sidebar_scroll_'>
                                   {!isTaggModal &&
                                          <Form.Group className='mb-3'>
                                                 <Form.Label className="mb-2" htmlFor="author_p_">{appContent?.moderation?.label_author_profile}</Form.Label>
                                                 <div className="d-flex align-items-center mb-5">
                                                        <div className="me-3 me-xxl-4">
                                                               <div className="symbol h-70px w-70px position-relative rounded-circle bg-light overflow-hidden">
                                                                      {authorImage && authorImage != '' ? <Image src={authorImage} alt={author} height={70} width={70} className="iobject-center-cover" load="lazy" fluid />
                                                                             : <span className="symbol-label bg-primary text-gray-100-alt fw-bold display-1 text-uppercase">{GET_FIRST_LETTER(author)}</span>}

                                                               </div>
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column">
                                                               <div className="d-flex align-items-center">
                                                                      <OverlayTrigger placement="top" overlay={<Tooltip>{appContent?.common?.edit ?? ""}</Tooltip>}>
                                                                             <Button variant='icon' size='sm' className="btn-active-primary m-1" aria-label="edit" onClick={this.handleOpen}>
                                                                                    <FontAwesomeIcon type={'solid'} icon={'pen'} />
                                                                             </Button>
                                                                      </OverlayTrigger>
                                                                      <OverlayTrigger placement="top" overlay={<Tooltip>{appContent?.common?.remove ?? ""}</Tooltip>}>
                                                                             <Button variant='icon' size='sm' className="btn-active-primary m-1" aria-label="delete" onClick={onClickRemoveAuthorImg}>
                                                                                    <FontAwesomeIcon type={'solid'} icon={'trash-can'} />
                                                                             </Button>
                                                                      </OverlayTrigger>
                                                               </div>
                                                               <div className="form-text mt-2">{formatTemplateText(appContent?.common?.allowed_file_types, {types: 'png, jpg, jpeg'})}</div>
                                                        </div>
                                                 </div>
                                          </Form.Group>}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="author_n_">{appContent?.moderation?.label_author_name} <span className='text-danger'>*</span></Form.Label>
                                          <Form.Control id="author_n_" name="author_n_" className={!author?.length ? 'is-invalid' : ''} placeholder={appContent?.moderation?.ph_author_name} value={author} onChange={onChangeAuthor} />
                                          {!author?.length ? <span className="invalid-feedback">{appContent?.moderation?.req_author_name}</span> : null}
                                   </Form.Group>}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="author_h_">{appContent?.moderation?.label_author_handle} <span className='text-danger'>*</span></Form.Label>
                                          <Form.Control id="author_h_" name="author_h_" className={!handle?.length ? 'is-invalid' : ''} placeholder={appContent?.moderation?.ph_author_handle} value={handle} onChange={onChangeHandle} />
                                          {!handle.length ? <span className="invalid-feedback">{appContent?.moderation?.req_author_handle}</span> : null}
                                   </Form.Group>}
                                   {isTaggModal ? <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="tags__">{appContent?.moderation?.label_add_tag}</Form.Label>
                                          <div className="mb-3">
                                                 <CreatableSelect className="selectpicker" placeholder={appContent?.moderation?.ph_tag} classNamePrefix="selectpicker" isClearable isLoading={isLoading} options={selectedOption} onChange={this.handleSelectChange} onInputChange={this.onSearchText} value={selectedOption} isValidNewOption={this.isValidNewOption} />
                                          </div>
                                          {/* <Button  onClick={()=>this.handleAddTagButton(searchPost)}>Add</Button> */}
                                          <div className="mb-3 mb-xl-4">
                                                 <div className="tags-group d-flex align-items-center flex-wrap">
                                                        {tagsArrayFun()}
                                                 </div>
                                          </div>
                                   </Form.Group> : ''}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="post_cont_">{appContent?.moderation?.label_content}</Form.Label>
                                          <Form.Control as={'textarea'} className={`textarea-min-row-7`} id="post_cont_" placeholder={appContent?.moderation?.ph_content} value={content} onChange={onChangeContent} />
                                   </Form.Group>}
                                   {
                                          modalData.type != 1 ?
                                                 <div className="mt-4 moderation-editThumbnail">
                                                        <Form.Label className="mb-2" htmlFor="s_pi">{modalData.type == 3 || modalData.type == 5 ? appContent?.moderation?.label_edit_thumbnail : appContent?.moderation?.label_edit_image}</Form.Label>
                                                        <AttachImage id="s_pi" imageStyle={{ paddingBottom: '66%' }} backgroundImage={modalData?.postFile} onClickTouploadImage={(url) => { updatePost(modalData['postFile'] = url); onChangePost(url, 'image') }} showStatus={true} />
                                                 </div> : null
                                   }

                                   {
                                          modalData.type == 3 || modalData.type == 5 ?
                                                 <div className="mt-4 moderation-editThumbnail">
                                                        <Form.Label className="mb-2" htmlFor="s_pi">{appContent?.moderation?.label_edit_video}</Form.Label>
                                                        <AttachImage id="s_pi" allowedFile={['video/*']} isVideo={true} imageStyle={{ paddingBottom: '66%' }} backgroundImage={modalData?.mediaUrl} onClickTouploadImage={(url) => { updatePost(modalData['mediaUrl'] = url); onChangePost(url, 'video') }} showStatus={true} />
                                                 </div>
                                                 : null
                                   }
                            </div>
                            <div className="assets_btn_wrapper p-4 f-center flex-column">
                                   {!isTaggModal ? <Button id="save_changes" className={`w-100 mb-2 ${loading ? 'spinner' : ''}`} disabled={loading || !(author && handle)} onClick={this.handleSave}>{appContent?.common?.save_changes}</Button>
                                          : <Button id="save_changes" className={`w-100 mb-2 ${loading ? 'spinner' : ''}`} disabled={loading} onClick={() => onSaveTag()}>{appContent?.common?.save_changes}</Button>}
                                   <Button variant='link' className='text-muted' onClick={handleClose}>{appContent?.common?.cancel}</Button>
                            </div>


                            <DashboardModal
                                   id="background-image"
                                   closeModalOnClickOutside
                                   closeAfterFinish
                                   open={this.state.uppyProfileOpen}
                                   onRequestClose={this.handleClose}
                                   uppy={uppyUploader.uppy}
                                   hideUploadButton={false}
                                   autoOpenFileEditor={false}
                                   plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
                                   animateOpenClose={true}
                                   proudlyDisplayPoweredByUppy={false}
                                   inline={false}
                                   showProgressDetails={true}
                                   height={750}
                                   browserBackButtonClose={true}
                                   showRemoveButtonAfterComplete={false}
                            />
                     </div>
              );
       }
}

export default EditAside;