import React, { Component } from 'react'
import { connect } from "react-redux";
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Form, Button, Badge } from 'react-bootstrap';
import { removeFilterSocialFeeds, removeFilterPostType, removeFilterOthers, removeFilterPostTags, removeDateRanges, removeAllFilterBody, removeAiFilter, removeSentimentFilter, removeRatingFilter, removePostLangFilter } from '../../../../../actions/moderationActions'
import HtmlParser from 'react-html-parser';

const IconWrapper = ({ children }) =>{
    return <Badge bg='gray-200' className="rounded-pill badge-light-secondary">
        {children}
    </Badge>
}
class FilterTab extends Component {
    onSelectFeeds = (data) => event => this.props.removeFilterSocialFeeds(data)
    onClickPostType = (value) => event => this.props.removeFilterPostType(value)
    onClickOther = (data) => event => this.props.removeFilterOthers(data)
    onClickRemovePostTags = (value) => event => this.props.removeFilterPostTags(value)
    onClickToDateRangeRemove = event => this.props.removeDateRanges()
    onClickAi = (value) => event => this.props.removeAiFilter(value)
    onClickSentiment = (value) => event => this.props.removeSentimentFilter(value)
    onClickRating = (value) => event => this.props.removeRatingFilter(value)
    onClickLanguage = () => this.props.removePostLangFilter()

    render() {
        const { filterBody } = this.props
        const appContent = window?.APP_CONTENT?.moderation ?? {};

        return (filterBody.socialFeed.length > 0 || filterBody.postType.length > 0 || filterBody.postTags.length > 0 || filterBody.other.length > 0 || Object.keys(filterBody.dateRanges).length > 0 || filterBody.recommendation.length > 0 || filterBody.sentiment.length > 0 || filterBody.rating.length > 0 || filterBody.languageCodes?.value) ? <div className="d-flex align-items-start mb-2">
            <span className="text-muted fw-medium me-2 my-2 text-nowrap">{appContent?.filter_by || ''}: </span>
            <div className="d-flex flex-wrap align-items-center">
                {
                    (Object.keys(filterBody.dateRanges).length > 0) ? <FilterPill label={`${filterBody.dateRanges.startDate} - ${filterBody.dateRanges.endDate}`} onClick={this.onClickToDateRangeRemove} /> : null
                }
                {
                    filterBody.postTags && filterBody.postTags.map((sItem, index) => {
                        return <FilterPill key={index} label={sItem} onClick={this.onClickRemovePostTags(sItem)} />
                    })
                }
                {
                    filterBody.socialFeed && filterBody.socialFeed.map((sItem, index) => {
                        return <FilterPill key={index} label={sItem.key == "onsiteupload" ? "SnapUp" : HtmlParser(sItem.key)} onClick={this.onSelectFeeds(sItem)} />
                    })
                }

                {
                    filterBody.postType && filterBody.postType.map((sItem, index) => {
                        if (sItem == "1") {
                            return <FilterPill key={index} label={appContent?.type_text || ''} onClick={this.onClickPostType(sItem)} />
                        }
                        else if (sItem == "2,4") {
                            return <FilterPill key={index} label={appContent?.type_image || ''} onClick={this.onClickPostType(sItem)} />
                        }
                        else if (sItem == "3,5") {
                            return  <FilterPill key={index} label={appContent?.type_video || ''} onClick={this.onClickPostType(sItem)} />
                        }
                        return null;
                    })
                }

                {
                    filterBody.other && filterBody.other.map((sItem) => {
                        if (sItem.key == "highlightFilter") {
                            return <FilterPill key={sItem.key} label={appContent?.highlight_post || ''} onClick={this.onClickOther(sItem)} />
                        }
                        else if (sItem.key == "pinFilter") {
                            return <FilterPill key={sItem.key} label={appContent?.other_pin || ''} onClick={this.onClickOther(sItem)} />
                        }
                        else if (sItem.key == "recentFilter") {
                            return <FilterPill key={sItem.key} label={appContent?.other_recent || ''} onClick={this.onClickOther(sItem)} />
                        }
                        else if (sItem.key == "retweetsFilter") {
                            return <FilterPill key={sItem.key} label={appContent?.other_retweets || ''} onClick={this.onClickOther(sItem)} />
                        }
                        else if (sItem.key == "tagProduct") {
                            return <FilterPill key={sItem.key} label={appContent?.type_ugc || ''} onClick={this.onClickOther(sItem)} />
                        }
                        return null;
                    })
                }

                {
                    filterBody.recommendation && filterBody.recommendation.map((sItem) => {
                        if (sItem == "1") {
                            return <FilterPill key={sItem} label={appContent?.ai_recom || ''} onClick={this.onClickAi(sItem)} />
                        }
                        else if (sItem == "2") {
                            return <FilterPill key={sItem} label={appContent?.ai_consider || ''} onClick={this.onClickAi(sItem)} />
                        }
                        else if (sItem == "3") {
                            return <FilterPill key={sItem} label={appContent?.ai_not_recom || ''} onClick={this.onClickAi(sItem)} />
                        }
                        return null;
                    })
                }

                {
                    filterBody.sentiment && filterBody.sentiment.map((sItem) => {
                        if (sItem == "1") {
                            return <FilterPill key={sItem} label={appContent?.sent_pos || ''} onClick={this.onClickSentiment(sItem)} />
                        }
                        else if (sItem == "2") {
                            return <FilterPill key={sItem} label={appContent?.sent_neut || ''} onClick={this.onClickSentiment(sItem)} />
                        }
                        else if (sItem == "3") {
                            return <FilterPill key={sItem} label={appContent?.sent_neg || ''} onClick={this.onClickSentiment(sItem)} />
                        }
                        return null;
                    })
                }
                {
                    filterBody.rating && filterBody.rating.map((sItem) => <FilterPill key={sItem} label={`${sItem} Star`} onClick={this.onClickRating(sItem)} />)
                }
                {
                    filterBody.languageCodes?.value ? <FilterPill label={filterBody.languageCodes?.label || ""} onClick={this.onClickLanguage} /> : null
                }
                <Button variant='dark' className='rounded-pill py-1 m-1' onClick={this.props.removeAllFilterBody}>{appContent?.reset || ''}</Button>
            </div>
        </div> : null
    }
}

const mapStateToProps = (state) => {
    return { moderation: state.moderation }
}
const mapDispatchToProps = dispatch => {
    return {
        removeFilterSocialFeeds: (data) => dispatch(removeFilterSocialFeeds(data)),
        removeFilterPostType: (data) => dispatch(removeFilterPostType(data)),
        removeFilterOthers: (data) => dispatch(removeFilterOthers(data)),
        removeFilterPostTags: (data) => dispatch(removeFilterPostTags(data)),
        removeDateRanges: () => dispatch(removeDateRanges()),
        removeAllFilterBody: () => dispatch(removeAllFilterBody()),
        removeAiFilter: (data) => dispatch(removeAiFilter(data)),
        removeSentimentFilter: (data) => dispatch(removeSentimentFilter(data)),
        removeRatingFilter: (data) => dispatch(removeRatingFilter(data)),
        removePostLangFilter: () => dispatch(removePostLangFilter())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterTab);

const FilterPill = ({ label, onClick }) => {
    return <IconWrapper>
        {label}
        <Button variant='icon' className='p-0 bg-gray-200 rounded-circle border-0 h-18px w-18px ms-2' onClick={onClick}>
            <FontAwesomeIcon type={'solid'} icon={'xmark'} />
        </Button>
    </IconWrapper>
}