import React, { useState } from 'react'
import { OverlayTrigger, Tooltip, Button, Modal, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { COPY_TO_CLIPBOARD, getEmbedCode } from '../../../../../utils';

const SingleEmbed = ({ show, data, handleClose }) => {
  const [state, setState] = useState({ activeEmbed: 1, width: 100, height: 100, wUnit: { value: 'percent', label: '%' }, hUnit: { value: 'percent', label: '%' }, loading: false, caption: 1, copied: false, header: 1 })
  const { activeEmbed, width, height, wUnit, hUnit, loading, caption, copied, header } = state;
  const appContent = window?.APP_CONTENT?.moderation ?? {};

  const copyText = (id) => {
    COPY_TO_CLIPBOARD(id)
    setState(prev => ({ ...prev, copied: true }))
    setTimeout(() => { setState(prev => ({ ...prev, copied: false })) }, 1000)
  }
  return (
    <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false} size="lg" centered>
      <Modal.Header closeButton>
        <Modal.Title>{appContent?.single_post_embed || ""}</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <div className="position-relative mb-4 embed_code embed_code_box iframe_embcode_box rounded-3">
          <div className='embed-border p-2'>
            <div className="embed_code_wrap">
              <div className="nav-ul mb-2">
                <button className={`btn btn-sm me-1 ${activeEmbed == 1 ? 'btn-primary' : ''}`} onClick={() => setState(prv => ({ ...prv, activeEmbed: 1 }))}>{appContent?.btn_litecode || ""}</button>
                <button className={`btn btn-sm ${activeEmbed == 2 ? 'btn-primary' : ''}`} onClick={() => setState(prv => ({ ...prv, activeEmbed: 2 }))}>{appContent?.btn_iframe || ""}</button>
              </div>
              <code className="form-control textarea-min-row-4 p-lg-4 fs-6" id="embed_code_" onClick={() => copyText('embed_code_')}>{getEmbedCode(1, activeEmbed, width, height, wUnit, hUnit, '', '', '', data?.id, data?.wallId, caption, header)}</code>
            </div>
          </div>
          <div className="position-absolute end-0 bottom-0 pe-5 pb-5">
            <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? appContent?.tooltip_copy || "" : appContent?.tooltip_copied || ""}</Tooltip>}>
              <Button variant="link" className="text-gray-300 fs-4 p-0" onClick={() => copyText('embed_code_')}>
                <FontAwesomeIcon type={'solid'} icon={!copied ? 'copy' : 'check'} iconClass={copied ? 'text-success' : ''} />
              </Button>
            </OverlayTrigger>
          </div>
        </div>
          {
            data.type != 1 ?
              <div className="d-flex align-items-center w-100 ms-2 mb-2">
                <Form.Label className="mb-0" htmlFor="inherit_"><span className="me-3 fs-7">{appContent?.label_caption || ""}</span> </Form.Label>
                <Form.Check type="switch" id="inherit_" className="mt-1" defaultValue={caption} checked={caption === 1} onChange={(e) => setState(prev => ({ ...prev, caption: Number(e.target.checked) }))} />
              </div> : null
          }
          <div className="d-flex align-items-center w-100 ms-2 mb-2">
            <Form.Label className="mb-0" htmlFor="inherit_"><span className="me-3 fs-7">{appContent?.label_header || ""}</span> </Form.Label>
            <Form.Check type="switch" id="inherit_" className="mt-1" defaultValue={header} checked={header === 1} onChange={(e) => setState(prev => ({ ...prev, header: Number(e.target.checked) }))} />
          </div>
      </Modal.Body>
      <Modal.Footer>
        <button type="button" className={`btn btn-primary cursor-pointer ${loading ? 'spinner spinner-gray' : ''}`} onClick={() => copyText('embed_code_')}>{copied ? appContent?.btn_copied || "" : appContent?.btn_copy_code || ""}</button>
      </Modal.Footer>
    </Modal>
  )
}

export default SingleEmbed