
import moment from 'moment'
import React, { Component } from 'react'
import { Modal, Button, Form, InputGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import RadialChart from './RadialChart'
import { Link } from 'react-router-dom';
import CardPreview from '../Elements/CardPreview';
import { COPY_TO_CLIPBOARD } from '../../../../../../utils';

export default class Pending extends Component {
    state={days:0, copied:false}

    componentDidMount(){ this.getNewdata() }

    getNewdata=()=>{
        const { ugcRequestData } = this.props
        var new_date = moment().diff(moment(ugcRequestData.ugccreated, "YYYY-MM-DD"), 'days');
        var ago = 30 - new_date;
        this.setState({days:ago})
    }
    componentDidUpdate(prevProps){
        if(prevProps.ugcRequestData.ugccreated !== this.props.ugcRequestData.ugccreated){
            this.getNewdata();
        }
    }
    copyText = (id) => {
        COPY_TO_CLIPBOARD(id)
        this.setState({copied:true})
        setTimeout(()=>{this.setState({copied:false})},1000)
    }
    render() {
        const { ugcData, ugcRequestData, onHide } = this.props
        const { days, copied} = this.state;
        const appContent = window?.APP_CONTENT?.moderation ?? {};
        return (<Modal size={'full'} show={true} className="assets_modal p-0" backdrop={false}>
            <Modal.Header className="justify-content-between py-0">
                <span className='text-muted fw-medium'>
                    <FontAwesomeIcon type={'solid'} icon={'user-clock'} iconClass={'me-1 text-warning'}/>
                    {appContent?.modal_pending_title || ''}
                </span>
                <div className='ms-5 d-flex justify-content-end'>
                    <Button variant='close' className="me-0 text-muted btn-active-primary" onClick={onHide}></Button>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex border-0 flex-lg-row flex-column">
                <div className="rights_modal_inner flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-lg-0 overflow-auto">
                    <h3 className='text-center mb-4'>{appContent?.modal_pending_oops || ''}</h3>
                    {ugcData ? <CardPreview ugcData={ugcData} /> : null}
                </div>
                <div className="rights_modal_sidebar position-relative">
                    <div className='p-lg-6 p-4 py-3 rights_modal_aside_wrap rights_no_wizard'>
                        <div className='mb-3'>
                            {days ? <RadialChart ago={ days} />:''}
                        </div>
                        <InputGroup className="mb-3">
                            <InputGroup.Text>
                                <FontAwesomeIcon type={'solid'} icon={'link'} />
                            </InputGroup.Text>
                            <Link id="rights_link" className="form-control form-control-lg" to={ugcRequestData.bitlyurl} target="_blank">{ugcRequestData.bitlyurl}</Link>
                        </InputGroup>
                        <Form.Group className="mb-5">
                            <Button size='lg' onClick={()=>this.copyText('rights_link')} className='w-100'>{copied? appContent?.btn_url_copied || '' : appContent?.btn_copy_url || ''}</Button>
                        </Form.Group>
                        <h5> {appContent?.steps_title || ''} </h5>
                        <ul className={`list-unstyled list-points fs-8`}>
                            <li className='mb-1'>{appContent?.steps_1 || ''}</li>
                            <li className='mb-1'>{appContent?.steps_2 || ''}</li>
                            <li className='mb-1'>{appContent?.steps_3 || ''}</li>
                            <li className='mb-1'>{appContent?.steps_4 || ''}</li>
                            <li className='mb-1'>{appContent?.steps_5 || ''}</li>
                        </ul>
                    </div>
                    <div className="d-flex justify-content-end p-4 border-top">
                        <Button className="btn-min-w" onClick={onHide}>{appContent?.btn_done || ''}</Button>
                    </div>
                </div>
            </Modal.Body>
            </Modal>
        )
    }
}
