import React, { useEffect, useMemo, useState } from 'react'
import { Modal } from 'react-bootstrap'
import ColorPicker from '../../CommonComponents/ColorPicker'
import { checkObj, isValidUrl } from '../../../../../utils'
import { POST_CTA_UPDATE } from '../../../../../actions/moderationActions'

const CtaModal = ({ isOpen, data, handleClose, listData }) => {

    const [ctaValue, setCtaValue] = useState({ buttonText: "", buttonUrl: "", ctaButtonColor: "", ctaTextColor: "", status: false, buttonTextError: false, buttonUrlError: false, textColorError: false, buttonColorError: false, })
    const [loading, setLoading] = useState(false)

    const appContent = useMemo(() => window?.APP_CONTENT?.moderation ?? {}, [])

    const callBack = (data) => {
        listData?.forEach(item => {
            if (item.referenceId == data?.referenceId && item.cta) {
                Object.assign(item.cta, {
                    id: data?.id,
                    buttonText: data?.buttonText,
                    buttonUrl: data?.buttonUrl,
                    ctaTextColor: data?.textColor,
                    ctaButtonColor: data?.buttonColor,
                    status: data?.status
                });
            }
        });

        handleClose();
        setLoading(false)
    }

    const updateCta = () => {
        setLoading(true)
        const { wallId, referenceId, feedId } = data;
        const { buttonText, buttonUrl, ctaButtonColor, ctaTextColor, status } = ctaValue;


        if (buttonText && buttonUrl && ctaButtonColor && ctaTextColor && isValidUrl(buttonUrl)) {
            let action = data?.cta?.id ? 2 : 1

            const payload = { id: data?.cta?.id, referenceId, wallId, feedId, buttonText, buttonUrl, textColor: ctaTextColor, buttonColor: ctaButtonColor, status, action }

            POST_CTA_UPDATE(payload, callBack)
        } else {
            const errorChanges = { buttonTextError: !buttonText, textColorError: String(ctaTextColor)?.length == 0, buttonColorError: String(ctaButtonColor)?.length == 0, buttonUrlError: !buttonUrl || !isValidUrl(buttonUrl)};
            setCtaValue(prev => ({ ...prev,  ...errorChanges }));
            setLoading(false)
        }
    }

    useEffect(() => {
        if (checkObj(data?.cta)) {
            const { buttonText, buttonUrl, ctaButtonColor, ctaTextColor, status } = data?.cta
            setCtaValue((prv) => ({ ...prv, buttonText, buttonUrl, ctaButtonColor, ctaTextColor, status }))
        }

    }, [data]);

    return (
        <Modal show={isOpen} onHide={handleClose} backdrop="static" keyboard={false} size="lg" centered>
            <Modal.Header closeButton>
                <Modal.Title>{appContent?.modal_title || ""}</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <form>
                    <div className="mb-3">
                        <label htmlFor="buttontext" className="form-label">
                            {appContent?.label_btn_text || ""} <span className="text-danger">*</span>
                        </label>
                        <input name="buttontext" id="buttontext" className="form-control" maxLength="25" placeholder={appContent?.ph_btn_text || ""} value={ctaValue?.buttonText} onChange={(e) => setCtaValue(prv => ({ ...prv, buttonText: e.target.value, buttonTextError: false }))} />
                        {ctaValue?.buttonTextError ? <div className="form-text text-danger">{appContent?.error_required || ""}</div> : null}
                    </div>
                    <div className="row">
                        <div className="col-md-6 mb-3">
                            <label htmlFor="color-ctaTextColor" className="form-label"> {appContent?.label_text_color || ""} <span className="text-danger">*</span>  </label>
                            <ColorPicker id="color-ctaTextColor" inheritStyle={false} defaultColor={ctaValue?.ctaTextColor} changeColor={(e) => { setCtaValue(prv => ({ ...prv, ctaTextColor: e, textColorError: false })) }} />
                            {ctaValue?.textColorError ? <div className="form-text text-danger">{appContent?.error_required || ""}</div> : null}
                        </div>
                        <div className="col-md-6 mb-3">
                            <label htmlFor="color-ctaButtonColor" className="form-label">
                                {appContent?.label_btn_color || ""} <span className="text-danger">*</span>
                            </label>
                            <ColorPicker id="color-ctaButtonColor" inheritStyle={false} defaultColor={ctaValue?.ctaButtonColor} changeColor={(e) => setCtaValue(prv => ({ ...prv, ctaButtonColor: e, buttonColorError: false }))} />
                            {ctaValue?.buttonColorError ? <div className="form-text text-danger">{appContent?.error_required || ""}</div> : null}
                        </div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="url" className="form-label">
                            URL <span className="text-danger">*</span>
                        </label>
                        <input type="url" id="url" name="buttonUrl" className="form-control" placeholder="https://example.com" maxLength="255" value={ctaValue?.buttonUrl} onChange={(e) => setCtaValue(prv => ({ ...prv, buttonUrl: e.target.value, buttonUrlError: isValidUrl(e.target.value) ? false : true }))} required />
                        {ctaValue.buttonUrlError ? <div className="form-text text-danger">{appContent?.error_url || ""}</div> : null}
                    </div>
                    <div className="mb-3 d-flex align-items-center justify-content-between">
                        <label htmlFor="CtaStatus" className="form-label mb-0">  {appContent?.label_status || ""} </label>
                        <div className="form-check form-switch">
                            <input type="checkbox" className="form-check-input" id="CtaStatus" name="data[status]" checked={ctaValue?.status} onChange={(e) => setCtaValue(prv => ({ ...prv, status: prv.status ? 0 : 1 }))} />
                        </div>
                    </div>
                </form>
            </Modal.Body>

            <Modal.Footer>
                <button type="button" className="btn btn-secondary" onClick={handleClose}> {window?.APP_CONTENT?.common?.cancel || "Cancel"} </button>
                <button type="button" className={`btn btn-primary cursor-pointer ${loading ? 'spinner spinner-gray' : ''}`} onClick={updateCta}> {window?.APP_CONTENT?.common?.save_changes || ""}</button>
            </Modal.Footer>
        </Modal>

    )
}

export default CtaModal